@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;800&display=swap);
.header__container {
  display: flex;
  min-height: 85vh;
  max-height: 100vh;
  align-items: center;
  justify-content: center;

  position: relative;
}
.header__container-sosial-media {
  display: flex;
  justify-content: space-between;
  border: 2px solid var(--color-secondary);
  border-radius: 5px;
  align-items: center;
  max-height: 12rem;

  z-index: 2;
}
.header__container-sosial-media-card {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  padding: 1rem 0.5rem;
  height: 150px;
  color: var(--color-text);
}
.header__container-sosial-media-card span {
  cursor: pointer;
}

.header__container-sosial-media-card span:hover {
  color: var(--color-secondary);
}

.header__container-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 80%;
  /* margin-left: 4rem; */
  z-index: 2;
}
.header__container-content-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  height: 261px;
}

.header__container-content-container h1 {
  font-size: 40px;
}
.header__container-content-container h4 {
  color: var(--color-text);
}
.header__container-content-container p {
  color: var(--color-text);
  /* max-width: 70%; */
  letter-spacing: -0.04em;
  font-size: 16px;
}
.header__container-content-container button {
  margin-bottom: 1.5rem;
}

.header__container-content button {
  display: block;
}
.header__container img {
  width: 40%;
  z-index: 1;
}

@media screen and (max-width: 1024px) {
  .header__container img {
    position: absolute;
    right: 4rem;
  }
}
@media screen and (max-width: 768px) {
  .header__container-content {
    margin-left: 2rem;
  }
  .header__container-content-container h1 {
    font-size: 36px;
  }

  .header__container-content-container p {
    max-width: 70%;
    letter-spacing: -0.04em;
    font-size: 14px;
  }
  .header__container-content-container button {
    margin-bottom: 1.5rem;
  }
}

@media screen and (max-width: 511px) {
  .header__container-content-container h1 {
    font-size: 30px;
  }

  .header__container-content-container p {
    max-width: 100%;
    letter-spacing: -0.04em;
    font-size: 12px;
  }
}
@media screen and (max-width: 425px) {
  .section__padding {
    padding: 1rem 2rem important!;
  }
}

/* #FF4820 */
