.about__container {
  display: flex;
  justify-content: center;
  padding: 4rem 4rem;
}

.about__container-left {
  display: flex;
  justify-content: center;
  padding: 0 2rem;
}
.about__container-left__img {
  display: flex;
  justify-content: center;
  align-items: center;
}
.about__container-left img {
  width: 375px;
}
.about__container-right {
