.navbar__container {
  display: flex;
  justify-content: space-between;
  padding: 2rem 4rem;
}

.navbar__container-links {
  display: flex;
  flex: 1 1;
  justify-content: space-between;
  align-items: center;
}
.navbar__container-links_container {
  display: flex;
  flex-direction: row;
  margin-right: 2rem;
}
.navbar__container-links_container a {
  margin: 0 1rem;
  text-decoration: none;
  font-size: 18px;
  color: #fff;
}

.navbar__conatiner-link-contact {
  display: flex;
  align-items: center;
}
.navbar__conatiner-link-contact a {
  padding: 0.7rem;
  border-radius: 5px;
  text-decoration: none;
}

.navbar__container-menu {
  display: none;
  position: relative;
  align-items: center;
}

.navbar__container-menu-container {
  position: absolute;
  background: var(--color-footer);
  top: 45px;
  right: 0;
  padding: 1rem 2.5rem;
  border-radius: 5px;

  z-index: 10;
}

.navbar__container-menu-links {
  display: flex;
  flex-direction: column;
  min-width: 150px;

  align-items: flex-end;
}
.navbar__container-menu-links a {
  text-decoration: none;
  color: #fff;
  margin: 1rem 0;
}

.navbar__container-menu-contact button {
  padding: 0.7rem 1rem;
  background: var(--color-scondary);
  border: none;
  border-radius: 5px;
  color: #fff;
}
.navbar__container-menu-contact {
  align-items: center;
  justify-content: flex-end;
  display: none;
}

@media screen and (max-width: 878px) {
  .navbar__container-links_container {
    display: none;
  }
  .navbar__container-menu {
    display: flex;
    margin-left: 1rem;
  }
}

@media screen and (max-width: 511px) {
  .navbar__conatiner-link-contact {
    display: none;
  }
  .navbar__container-menu-contact {
    display: flex;
  }
}

.service__container-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.service__container-content__card {
  /* display: flex; */
  /* flex-wrap: wrap; */
  color: #000;
  background: #fff;
  border-radius: 5px;
  padding: 2rem;
  width: 277px;
  height: 277px;
  margin: 1rem;
}
.service__container-content__card-content {
  margin-top: 1rem;
}
.service__container-content__card-content p {
  font-size: 18px;
}

.experience__container-content-timeline__container {
  position: relative;
  margin: 0 auto;
  display: flex;
  justify-content: center;
}

.cd-container {
  width: 90%;
  max-width: 1080px;
  margin: 0 auto;
  background: var(--color-footer);
  padding: 0 10%;
  border-radius: 2px;
}
.cd-container::after {
  content: "";
  display: table;
  clear: both;
}
@media only screen and (min-width: 375px) {
  .experience__container.section__padding {
    padding: 2rem;
  }
}
@media only screen and (min-width: 768px) {
  .experience__container.section__padding {
    padding: 4rem;
  }
}

/* -------------------------------- 
 
 Main components 
 
 -------------------------------- */

#cd-timeline {
  position: relative;
  padding: 2.8em 0;
  margin-top: 2em;
  margin-bottom: 2em;
}
#cd-timeline::before {
  content: "";
  position: absolute;
  top: 0;
  left: 25px;
  height: 100%;
  width: 4px;
  background: #ff4820;
}
@media only screen and (min-width: 1170px) {
  #cd-timeline {
    margin-top: 3em;
    margin-bottom: 3em;
  }
  #cd-timeline::before {
    left: 50%;
    margin-left: -2px;
  }
}

.cd-timeline-block {
  position: relative;
  margin: 2em 0;
}
.cd-timeline-block:after {
  content: "";
  display: table;
  /* color: #fff; */
  clear: both;
}
.cd-timeline-block:first-child {
  margin-top: 0;
}
.cd-timeline-block:last-child {
  margin-bottom: 0;
}
@media only screen and (min-width: 1170px) {
  .cd-timeline-block {
    margin: 4em 0;
  }
  .cd-timeline-block:first-child {
    margin-top: 0;
  }
  .cd-timeline-block:last-child {
    margin-bottom: 0;
  }
}

.cd-timeline-img {
  position: absolute;
  top: 8px;
  left: 12px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  box-shadow: 0 0 0 4px #ff4820, inset 0 2px 0 rgba(0, 0, 0, 0.08),
    0 3px 0 4px rgba(0, 0, 0, 0.05);
}
.cd-timeline-img.cd-movie {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-weight: 800;
}
.cd-timeline-img {
  background: #ff4820;
}
@media only screen and (min-width: 1170px) {
  .cd-timeline-img {
    width: 30px;
    height: 30px;
    left: 50%;
    margin-left: -15px;
    margin-top: 15px;
    /* Force Hardware Acceleration in WebKit */
    -webkit-transform: translateZ(0);
    -webkit-backface-visibility: hidden;
  }
}

.cd-timeline-content {
  position: relative;
  margin-left: 60px;
  margin-right: 30px;
  background: #fff;
  border-radius: 2px;
  padding: 1em;
}
@media (max-width: 500px) {
  .section__padding {
    padding: 2rem;
  }

  .timeline-content-info-title,
  .timeline-content-info-date {
    display: block;
    width: 100%;
  }
}
.cd-timeline-content:after {
  content: "";
  display: table;
  clear: both;
}
.cd-timeline-content h2 {
  color: #333;
  margin-top: 0;
  margin-bottom: 5px;
}
.cd-timeline-content p,
.cd-timeline-content .cd-date {
  font-size: 13px;
  font-size: 0.8125rem;
}
.cd-timeline-content .cd-date {
  display: inline-block;
}
.cd-timeline-content.cd-date h4 {
  margin-top: 1rem;
}
.cd-date h5 {
  margin-bottom: 0.5rem;
  display: none;
}
.cd-date h4 {
  color: #ff4820;
}
.cd-timeline-content p {
  margin: 0.2em 0;
  line-height: 1.6;
}

.cd-timeline-content .cd-date p {
  color: var(--color-text);
}

.cd-timeline-content::before {
  content: "";
  position: absolute;
  top: 16px;
  right: 100%;
  height: 0;
  width: 0;
  border: 7px solid transparent;
  border-right: 7px solid #fff;
}

@media only screen and (min-width: 768px) {
  .section__padding {
    padding: 2rem;
  }

  .cd-timeline-content h2 {
    font-size: 20px;
    font-size: 1.25rem;
  }
  .cd-timeline-content p {
    font-size: 16px;
    font-size: 1rem;
  }
  .cd-timeline-content .cd-read-more,
  .cd-timeline-content .cd-date {
    font-size: 14px;
    font-size: 0.875rem;
  }
}
@media only screen and (min-width: 1170px) {
  .cd-timeline-content {
    /* color: white; */
    margin-left: 0;
    padding: 1.6em;
    width: 36%;
    margin: 0 5%;
  }
  .cd-timeline-content::before {
    top: 24px;
    left: 100%;
    border-color: transparent;
    border-left-color: #333c42;
  }
  .cd-timeline-content .cd-date {
    position: absolute;
    width: 100%;
    /* left: 122%;
    top: 6px; */
    left: 140%;
    top: 27px;
    font-size: 16px;
    font-size: 1rem;
  }
  .cd-timeline-block:nth-child(even) .cd-timeline-content {
    float: right;
  }
  .cd-timeline-block:nth-child(even) .cd-timeline-content::before {
    top: 24px;
    left: auto;
    right: 100%;
    border-color: transparent;
    border-right-color: #333c42;
  }
  .cd-timeline-block:nth-child(even) .cd-timeline-content .cd-read-more {
    float: right;
  }
  .cd-timeline-block:nth-child(even) .cd-timeline-content .cd-date {
    left: auto;
    /* right: 122%; */
    right: 140%;
    top: 27px;
    text-align: right;
  }
}

